import { ViewsManager } from "app/shared/resources/views-manager";
import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatDialogRef } from "@angular/material/dialog";
import { MatTableDataSource } from "@angular/material/table";
import {
  IEmployeeDependantModel,
  IEmployeeDependantRequest,
} from "app/personnel-setup/models/emp-personal-info/dependants";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { CustomValidator } from "app/shared/validators/custom.validator";
import { CustomValidators } from "ngx-custom-validators";
import { EmployeeDependantService } from "app/personnel-setup/services/employees/personal-info/employee-dependant.service";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import { employeeDependentToMap } from "app/personnel-setup/mapper/employee-personal-info";
import { IPageParams } from "app/shared/interfaces/params";

@Component({
  selector: "app-employee-dependants-dialog",
  templateUrl: "./emp-dependants-dialog.component.html",
  styleUrls: ["./emp-dependants-dialog.component.scss"],
})
export class EmployeeDependantsDialogComponent
  extends AppHelpers
  implements OnInit
{
  employeeId: number = 0;
  updated: boolean = false;

  mandatoryColomns: string[] = [
    "Ser",
    // "id",
    "dependentName",
    "relationship",
    "birthDate",
    "phoneNumber",
    "isEmergcy",
    "actions",
  ];
  optionalColomns: string[] = [];
  dataSource: MatTableDataSource<IEmployeeDependantModel>;

  isLoaded: boolean = false;

  form: FormGroup;
  submitted: boolean = false;

  constructor(
    private employeeDependantService: EmployeeDependantService,
    public dialogRef: MatDialogRef<EmployeeDependantsDialogComponent>
  ) {
    super();
    this.employeeId = EmpGeneralInfoService.EmployeeId;

    this.rowFunctions = this.getSubRowFunctions(
      this.getParentViewName,
      ViewsManager.emp_dependants_sec
    );
  }

  ngOnInit(): void {
    this.form = this.fb.group({
      id: [0, { nonNullable: true }],
      name: [null, [Validators.required, CustomValidator.whiteSpace]],
      relationship_ID: [null, [Validators.required]],
      birthDate: [null, [CustomValidators.date]],
      phoneNumber: [
        null,
        [
          Validators.pattern(/^(\(?\+?[0-9]*\)?)?[0-9_ \-\(\)\S*$]*$/),
          CustomValidator.MobileStartsWith,
          CustomValidator.mobileLength,
        ],
      ],
      isEmergcy: [null],
    });

    this.dialogRef.backdropClick().subscribe((ele) => {
      this.onCloseDialog();
    });
  }

  onCloseDialog() {
    this.dialogRef.close(this.updated);
  }

  get formCtrls() {
    return this.form.controls;
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany()) {
      if (this.employeeId > 0) this.getList(this.firstPage);
    }
  }

  getList(page: number, pageSize?: number) {
    this.page.pageIndex = page;
    if (pageSize) this.page.dialogSize = pageSize;
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.dialogSize,
    };
    this.employeeDependantService
      .getList(this.employeeId, params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IEmployeeDependantModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(
            employeeDependentToMap(res.data)
          );
          this.page.length = res.meta.totalItemCount;
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IEmployeeDependantRequest = {
        employeeData: {
          person_ID: this.employeeId,
          id: this.form.value.id,
          flower_Name: this.form.value.name,
          relationship_ID: this.form.value.relationship_ID,
          birthDate: this.form.value.birthDate,
          phoneNumber: this.form.value.phoneNumber,
          isEmergcy: this.form.value.isEmergcy ?? false,
        },
      };

      model.employeeData.id == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IEmployeeDependantRequest) {
    this.showLoading();
    this.employeeDependantService
      .create(model)
      .subscribe((res: IResponse<IEmployeeDependantModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getList(this.currentPage);
          this.updated = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  update(model: IEmployeeDependantRequest) {
    this.showLoading();
    this.employeeDependantService
      .update(model)
      .subscribe((res: IResponse<IEmployeeDependantModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.clearForm();
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          this.getList(this.currentPage);
          this.updated = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  employeeDependantToEdit(employeeDependantInfo: IEmployeeDependantModel) {
    this.form.patchValue(employeeDependantInfo);
    this.formCtrls.name.setValue(employeeDependantInfo.flower_Name);
    this.formCtrls.birthDate.setValue(
      employeeDependantInfo.originalBirthDateFormat
    );
    // console.log(this.form.value);
  }

  onDelete(employeeDependantInfo: IEmployeeDependantModel): void {
    let msg = employeeDependantInfo.flower_Name;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(employeeDependantInfo);
    });
  }

  delete(employeeDependantInfo: IEmployeeDependantModel) {
    this.showLoading();
    this.employeeDependantService
      .Delete(employeeDependantInfo.id)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          if (this.dataSource.data.length == 1 && this.currentPage > 1) {
            // if last item in list: get previous page
            this.getList(this.prevPage, this.page.dialogSize);
          } else {
            // if not last item in list: get current page
            this.getList(this.currentPage, this.page.dialogSize);
          }
          this.updated = true;
          if (employeeDependantInfo.id == this.formCtrls.id.value)
            this.clearForm();
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
  }
}
