import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatDialogRef } from "@angular/material/dialog";
import { MatTableDataSource } from "@angular/material/table";
import { empHiringDocumentDisplay } from "app/personnel-setup/mapper/emp-employment";
import {
  IEmpHiringDocumentAddRequest,
  IEmpHiringDocumentDeleteRequest,
  IEmpHiringDocumentDisplayModel,
  IEmpHiringDocumentEditRequest,
  IEmpHiringDocumentModel,
  IEmpHiringDocumentRequest,
} from "app/personnel-setup/models/emp-employment-info/hiring-documents";
import { EmployeeHiringDocumentService } from "app/personnel-setup/services/employees/employement-information/employee-hiring-document.service";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import { RadioButtonModel } from "app/shared/components/resources/dynamic-form-field/radio-button/radio-button.component";
import { IAttachIconColumnData } from "app/shared/components/resources/material-table/material-table.component";
import { EEmpHireDocType } from "app/shared/enums/emp-hire-doc-type.enum";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IFileData } from "app/shared/interfaces/files";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { ViewsManager } from "app/shared/resources/views-manager";

@Component({
  selector: "emp-hiring-documents-dialog",
  templateUrl: "./emp-hiring-documents-dialog.component.html",
  styleUrls: ["./emp-hiring-documents-dialog.component.scss"],
})
export class EmpHiringDocumentsDialogComponent
  extends AppHelpers
  implements OnInit
{
  employeeInstanceId: number = 0;
  updated: boolean = false;

  mandatoryColomns: string[] = [
    "Ser",
    "documentName",
    "attachmentType",
    "attachment",
    "actions",
  ];
  optionalColomns: string[] = [];
  dataSource: MatTableDataSource<IEmpHiringDocumentDisplayModel>;

  isLoaded: boolean = false;

  form: FormGroup;
  submitted: boolean = false;
  removeFiles: boolean = false;

  selectedAttachmentTypeId: number = 0;
  selectedis_Attached: number = 0;

  constructor(
    private employeeHiringDocumentService: EmployeeHiringDocumentService,
    public dialogRef: MatDialogRef<EmpHiringDocumentsDialogComponent>
  ) {
    super();
    this.employeeInstanceId = EmpGeneralInfoService.EmployeeInstanceId;

    this.rowFunctions = this.getSubRowFunctions(
      this.getParentViewName,
      ViewsManager.emp_hiring_documents_sec
    );
  }

  ngOnInit(): void {
    this.form = this.fb.group({
      id: [0, { nonNullable: true }],
      att_Type_ID: [null, [Validators.required]],
      file: [null /* , [Validators.required] */],
      is_Attached: [EEmpHireDocType.attached, [Validators.required]],
      attachmentID: [null],
    });

    this.dialogRef.backdropClick().subscribe((ele) => {
      this.onCloseDialog();
    });
  }

  get formCtrls() {
    return this.form.controls;
  }

  onCloseDialog() {
    this.dialogRef.close(this.updated);
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.employeeInstanceId > 0 && this.checkNoBusinessOrCompany())
      this.getList(this.firstPage);
  }

  getList(page: number, pageSize?: number) {
    this.dataSource = new MatTableDataSource(null);
    this.isLoaded = false;
    this.page.pageIndex = page;
    if (pageSize) this.page.dialogSize = pageSize;
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.dialogSize,
    };
    this.employeeHiringDocumentService
      .getList(this.employeeInstanceId, params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IEmpHiringDocumentModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          let mapedData = empHiringDocumentDisplay(res.data);
          mapedData.forEach(
            (ele) => (ele.attachmentType = this.translate(ele.attachmentType))
          );
          this.dataSource = new MatTableDataSource(mapedData);
          this.page.length = res.meta.totalItemCount;
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    this.removeFiles = false;
    if (this.form.valid) {
      const model: IEmpHiringDocumentRequest = {
        Person_Instance_ID: this.employeeInstanceId,
        files: this.form.value.file,
        Is_Attached: this.form.value.is_Attached,
      };

      this.formCtrls.id.value == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IEmpHiringDocumentRequest) {
    let addModel: IEmpHiringDocumentAddRequest = {
      ...model,
      Attachment_Type_ID: this.form.value.att_Type_ID,
    };
    let formData = HelpersFunctions.objectToFormData(addModel);

    this.showLoading();
    this.employeeHiringDocumentService
      .create(formData)
      .subscribe((res: IResponse<IEmpHiringDocumentModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getList(this.currentPage);
          this.updated = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  update(model: IEmpHiringDocumentRequest) {
    if (this.formCtrls.file.value?.id > 0) model.files = null;

    let editModel: IEmpHiringDocumentEditRequest = {
      ...model,
      Att_Type_ID: this.form.value.att_Type_ID,
      AttachmentID:
        this.selectedis_Attached != model.Is_Attached
          ? this.form.value.attachmentID
          : 0,
      Att_Type_ID_Old: this.selectedAttachmentTypeId,
    };
    let formData = HelpersFunctions.objectToFormData(editModel);

    this.showLoading();
    this.employeeHiringDocumentService
      .update(formData)
      .subscribe((res: IResponse<IEmpHiringDocumentModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.clearForm();
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          this.getList(this.currentPage);
          this.updated = true;
          this.selectedAttachmentTypeId = 0;
          this.selectedis_Attached = 0;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  employeeHiringDocumentToEdit(hiringDocument: IEmpHiringDocumentDisplayModel) {
    this.form.patchValue(hiringDocument);

    if (hiringDocument.attachmentID) {
      // get file size && type
      this.showLoading();
      this.fileService
        .downloadByAttachId(hiringDocument.attachmentID)
        .subscribe((res) => {
          let file: IFileData = {
            id: hiringDocument.attachmentID,
            path: hiringDocument.attachment,
            name: "",
            type: res.type,
            size: res.size,
          };
          this.formCtrls.file.setValue(file);
          this.hideLoading();
        });
    } else this.formCtrls.file.setValue(null);

    // not real id >> only to detect edit mode
    this.formCtrls.id.setValue(hiringDocument.att_Type_ID);
    // to use in edit request
    this.selectedAttachmentTypeId = hiringDocument.att_Type_ID;
    this.selectedis_Attached = hiringDocument.is_Attached;
  }

  onDelete(hiringDocument: IEmpHiringDocumentDisplayModel): void {
    let msg = hiringDocument.documentName;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(hiringDocument);
    });
  }

  delete(hiringDocument: IEmpHiringDocumentDisplayModel) {
    let deleteParams: IEmpHiringDocumentDeleteRequest = {
      perInsId: this.employeeInstanceId,
      DocTypeId: hiringDocument.att_Type_ID,
      AttachmentID: hiringDocument.attachmentID,
    };

    this.showLoading();
    this.employeeHiringDocumentService
      .Delete(deleteParams)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          if (this.dataSource.data.length == 1 && this.currentPage > 1) {
            // if last item in list: get previous page
            this.getList(this.prevPage, this.page.dialogSize);
          } else {
            // if not last item in list: get current page
            this.getList(this.currentPage, this.page.dialogSize);
          }
          this.updated = true;
          if (
            hiringDocument.att_Type_ID == this.selectedAttachmentTypeId &&
            // if edit
            this.formCtrls.id.value > 0
          )
            this.clearForm();
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  deleteFile(file: IFileData) {
    this.showLoading();
    this.employeeHiringDocumentService
      .DeleteFile(this.employeeInstanceId, file.id)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          this.getList(this.currentPage);
          this.formCtrls.file.reset();
        } else {
          this.errorList(res.message.messages);
          this.hideLoading();
        }
      });
  }

  clearForm() {
    this.form.reset({ id: 0, is_Attached: EEmpHireDocType.attached });
    this.submitted = false;
    this.removeFiles = true;
  }

  attachIconColumnData: IAttachIconColumnData = {
    columnName: "attachment",
    icon: "fas fa-file-alt",
  };

  hireDocType = EEmpHireDocType;
  hireDocTypeRadioList: RadioButtonModel[] = [
    {
      value: EEmpHireDocType.attached,
      label: EEmpHireDocType.getValue(EEmpHireDocType.attached),
      cssClass: "col-md-4 col-sm-6 col-12",
    },
    {
      value: EEmpHireDocType.handDelivery,
      label: EEmpHireDocType.getValue(EEmpHireDocType.handDelivery),
      cssClass: "col-md-4 col-sm-6 col-12",
    },
    {
      value: EEmpHireDocType.notNeed,
      label: EEmpHireDocType.getValue(EEmpHireDocType.notNeed),
      cssClass: "col-md-4 col-sm-6 col-12",
    },
  ];
}
