import { Component, OnInit } from "@angular/core";
import { empVacationDisplay } from "app/personnel-setup/mapper/emp-balances";
import { IEmpVacationModel } from "app/personnel-setup/models/emp-balances/vacation";
import { EmployeeVacationService } from "app/personnel-setup/services/employees/employee-balances/employee-vacation.service";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { EmpVacationDialogComponent } from "../emp-vacation-dialog/emp-vacation-dialog.component";

@Component({
  selector: "emp-vacation-card",
  templateUrl: "./emp-vacation-card.component.html",
  styleUrls: ["./emp-vacation-card.component.scss"],
})
export class EmpVacationCardComponent extends AppHelpers implements OnInit {
  employeeInstanceId: number = 0;
  vacations: IEmpVacationModel[] = [];

  isLoaded: boolean = false;

  constructor(private employeeVacationService: EmployeeVacationService) {
    super();
    this.employeeInstanceId = EmpGeneralInfoService.EmployeeInstanceId;
    EmpGeneralInfoService.employeeInstanceId$.subscribe((empInstanceId) => {
      this.employeeInstanceId = empInstanceId;
      if (this.employeeInstanceId > 0 && this.checkNoBusinessOrCompany()) {
        this.isLoaded = false;
        this.getCard();
      }
    });
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    this.vacations = [];
    if (this.employeeInstanceId > 0 && this.checkNoBusinessOrCompany())
      this.getCard();
    else this.isLoaded = true;
  }

  getCard() {
    this.vacations = [];
    this.employeeVacationService
      .getCard(this.employeeInstanceId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IEmpVacationModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          if (res.data.length > 0) {
            let list1: IEmpVacationModel[] = [];
            let list2: IEmpVacationModel[] = [];
            // annual & casual vacations
            res.data.forEach((vacation) => {
              if (this.isAnnualOrCasualVacations(vacation))
                list1.push(vacation);
              else list2.push(vacation);
            });
            let sortedList: IEmpVacationModel[] = [
              // sort by end date (from new to old) > without annual & casual
              ...list1,
              ...list2
                .sort(
                  (a, b) =>
                    new Date(b.closing_balance_date).getTime() -
                    new Date(a.closing_balance_date).getTime()
                )
                .slice(0, 5 - list1.length),
            ];
            // sort by end date (from new to old) > all list
            sortedList.sort(
              (a, b) =>
                new Date(b.closing_balance_date).getTime() -
                new Date(a.closing_balance_date).getTime()
            );
            this.vacations = empVacationDisplay(sortedList).reverse();
          } else {
            let sortedList: IEmpVacationModel[] = res.data;
            sortedList.sort(
              (a, b) =>
                new Date(b.closing_balance_date).getTime() -
                new Date(a.closing_balance_date).getTime()
            );
            this.vacations = empVacationDisplay(sortedList).reverse();
          }
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  openAddEditDialog(): void {
    this.dialogsService
      .addEditDialog(EmpVacationDialogComponent)
      .afterClosed()
      .subscribe((updated: boolean) => {
        if (updated) {
          this.isLoaded = false;
          this.getCard();
        }
      });
  }

  isAnnualOrCasualVacations(vacation: IEmpVacationModel) {
    return (
      vacation.vacation_EnName.toLocaleLowerCase().includes("annual") ||
      vacation.vacation_ArName.toLocaleLowerCase().includes("annual") ||
      vacation.vacation_EnName.toLocaleLowerCase().includes("casual") ||
      vacation.vacation_ArName.toLocaleLowerCase().includes("casual")
    );
  }
}
