import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatDialogRef } from "@angular/material/dialog";
import { MatTableDataSource } from "@angular/material/table";
import { empCustodyDisplay } from "app/personnel-setup/mapper/emp-employment";
import {
  IEmpCustodyModel,
  IEmpCustodyRequest,
} from "app/personnel-setup/models/emp-employment-info/custodies";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import { EmployeeCustodiesService } from "app/personnel-setup/services/employees/employement-information/employee-custodies.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { ViewsManager } from "app/shared/resources/views-manager";
import { CustomValidator } from "app/shared/validators/custom.validator";

@Component({
  selector: "app-emp-custodies-dialog",
  templateUrl: "./emp-custodies-dialog.component.html",
  styleUrls: ["./emp-custodies-dialog.component.scss"],
})
export class EmpCustodiesDialogComponent extends AppHelpers implements OnInit {
  // employeeId: number = 0;
  employeeInstanceId: number = 0;
  updated: boolean = false;

  mandatoryColomns: string[] = [
    "Ser",
    // "id",
    "custodies",
    "froM_DATE",
    "endDate",
    "notes",
    "actions",
  ];
  optionalColomns: string[] = [];
  dataSource: MatTableDataSource<IEmpCustodyModel>;

  isLoaded: boolean = false;

  form: FormGroup;
  submitted: boolean = false;

  constructor(
    private employeeCustodiesService: EmployeeCustodiesService,
    public dialogRef: MatDialogRef<EmpCustodiesDialogComponent>
  ) {
    super();
    // this.employeeId = EmpGeneralInfoService.EmployeeId;
    this.employeeInstanceId = EmpGeneralInfoService.EmployeeInstanceId;

    this.rowFunctions = this.getSubRowFunctions(
      this.getParentViewName,
      ViewsManager.emp_custodies_sec
    );
  }

  ngOnInit(): void {
    this.form = this.fb.group(
      {
        id: [0, { nonNullable: true }],
        covenantId: [null, [Validators.required]],
        froM_DATE: [null, [Validators.required]],
        endDate: [null/* , [Validators.required] */],
        notes: [null],
      },
      {
        validator: CustomValidator.compareDates("froM_DATE", "endDate"),
      }
    );

    this.dialogRef.backdropClick().subscribe((ele) => {
      this.onCloseDialog();
    });
  }

  onCloseDialog() {
    this.dialogRef.close(this.updated);
  }

  get formCtrls() {
    return this.form.controls;
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany()) {
      if (this.employeeInstanceId > 0) this.getList(this.firstPage);
    }
  }

  getList(page: number, pageSize?: number) {
    this.page.pageIndex = page;
    if (pageSize) this.page.dialogSize = pageSize;
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.dialogSize,
    };
    this.employeeCustodiesService
      .getList(this.employeeInstanceId, params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IEmpCustodyModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(empCustodyDisplay(res.data));
          this.page.length = res.meta.totalItemCount;
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IEmpCustodyRequest = {
        EmployeeData: {
          PersonInstanceId: this.employeeInstanceId,
          ID: this.form.value.id,
          CovenantId: this.form.value.covenantId,
          FromDate: HelpersFunctions.mapDateReverse(this.form.value.froM_DATE),
          ToDate: HelpersFunctions.mapDateReverse(this.form.value.endDate),
          Notes: this.form.value.notes,
        },
      };

      model.EmployeeData.ID == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IEmpCustodyRequest) {
    this.showLoading();
    this.employeeCustodiesService
      .create(model)
      .subscribe((res: IResponse<IEmpCustodyModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getList(this.currentPage);
          this.updated = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  update(model: IEmpCustodyRequest) {
    this.showLoading();
    this.employeeCustodiesService
      .update(model)
      .subscribe((res: IResponse<IEmpCustodyModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.clearForm();
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          this.getList(this.currentPage);
          this.updated = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  employeeCustodyToEdit(employeeCustodyInfo: IEmpCustodyModel) {
    this.form.patchValue(employeeCustodyInfo);
    this.formCtrls.froM_DATE.setValue(employeeCustodyInfo.fromDate);
    this.formCtrls.endDate.setValue(employeeCustodyInfo.toDate);
    // console.log(this.form.value);
  }

  onDelete(employeeCustodyInfo: IEmpCustodyModel): void {
    let msg = employeeCustodyInfo.custodies;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(employeeCustodyInfo);
    });
  }

  delete(employeeCustodyInfo: IEmpCustodyModel) {
    this.showLoading();
    this.employeeCustodiesService
      .Delete(employeeCustodyInfo.id)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          if (this.dataSource.data.length == 1 && this.currentPage > 1) {
            // if last item in list: get previous page
            this.getList(this.prevPage, this.page.dialogSize);
          } else {
            // if not last item in list: get current page
            this.getList(this.currentPage, this.page.dialogSize);
          }
          this.updated = true;
          if (employeeCustodyInfo.id == this.formCtrls.id.value)
            this.clearForm();
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
  }
}
