import {Component, OnInit} from "@angular/core";
import {EmpCustodiesDialogComponent} from "../emp-custodies-dialog/emp-custodies-dialog.component";
import {AppHelpers} from "app/shared/resources/app-helpers";
import {IEmpCustodyModel} from "app/personnel-setup/models/emp-employment-info/custodies";
import {
  EmployeeCustodiesService
} from "app/personnel-setup/services/employees/employement-information/employee-custodies.service";
import {IResponse} from "app/shared/interfaces/responces";
import {MessageTypes} from "app/shared/enums/message-types.enum";
import {EmpGeneralInfoService} from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import {empCustodyDisplay} from "app/personnel-setup/mapper/emp-employment";

@Component({
  selector: "app-emp-custodies-card",
  templateUrl: "./emp-custodies-card.component.html",
  styleUrls: ["./emp-custodies-card.component.scss"],
})
export class EmpCustodiesCardComponent extends AppHelpers implements OnInit {
  // employeeId: number = 0;
  employeeInstanceId: number = 0;
  custodies: IEmpCustodyModel[];

  isLoaded: boolean = false;

  constructor(private employeeCustodiesService: EmployeeCustodiesService) {
    super();
    // this.employeeId = EmpGeneralInfoService.EmployeeId;
    this.employeeInstanceId = EmpGeneralInfoService.EmployeeInstanceId;
    EmpGeneralInfoService.employeeInstanceId$.subscribe((empInstanceId) => {
      this.employeeInstanceId = empInstanceId;
      if (this.employeeInstanceId > 0 && this.checkNoBusinessOrCompany()) {
        this.isLoaded = false;
        this.getAllByEmployeeId();
      }
    });
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    this.custodies = null;
    if (this.employeeInstanceId > 0 && this.checkNoBusinessOrCompany())
      this.getAllByEmployeeId();
    else this.isLoaded = true;
  }

  getAllByEmployeeId() {
    this.custodies = null;
    this.employeeCustodiesService
      .getAll(this.employeeInstanceId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IEmpCustodyModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          // show only first three custodies
          this.custodies = empCustodyDisplay(res.data.slice(0, 3));
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  openAddEditDialog(): void {
    this.dialogsService
      .addEditDialog(EmpCustodiesDialogComponent)
      .afterClosed()
      .subscribe((updated: boolean) => {
        if (updated) {
          this.isLoaded = false;
          this.getAllByEmployeeId();
        }
      });
  }
}
