import { IContractModel } from "app/personnel-setup/models/emp-employment-info/contract";
import { IJobTitleModel } from "app/org-management/model/job-title";
import { IOrganizationModel } from "app/org-management-setup/models/organizations";
import { ICoreStatusPropertiesModel, IJobStatusModel } from "app/hr-setup/models/emp-job-status";
import { IPersonInstanceModel } from "app/personnel-setup/models/employees";
import { IPersonInstanceOrg } from "app/org-management/model/org-structure";


// all request
export interface IEmpJobStatusPositionRequest {
  employee_Status: PositionReqEmployee_Status;
  core_Person_Instance_Org: PositionReqPerson_Instance_Org;
  core_Person_Instance_Job_Title: PositionReqPerson_Instance_Job_Title;
}


//  employee_Status request
interface PositionReqEmployee_Status {
  PersonInstanceStatus: {
    id: number;
    Person_Instance_ID: number;
    Status_ID: number;
    From_Date: string;
    StatusPropertiesId: number
  };
  NewDefaultCompany: number;
}

// core_Person_Instance_Org request
interface PositionReqPerson_Instance_Org {
  id: number;
  Person_Instance_ID: number;
  Org_ID: number;
  From_Date: string;
}

// core_Person_Instance_Job_Title request
interface PositionReqPerson_Instance_Job_Title {
  id: number;
  Person_Instance_ID: number;
  Job_Title_ID: number;
  From_Date: string;
  Is_Manpower_Affected: boolean;
}


// card
export interface IEmpJobStatusPositionCardModel {
  contract_Duration: IDurationModel;
  time_In_Company: IDurationModel;
  core_Contract: IContractModel;
  core_Person_Instance_Status: IPersonInstanceStatusModel;
  positions: {
    core_Job_Title: IJobTitleModel;
    core_Org: IOrganizationModel;
    isThereMore: boolean;
    jobFromDate: string;
    orgFromDate: string;
    time_In_Position: IDurationModel;
  };
}

// card display
export interface IEmpStatusCardDisplayModel {
  status_name: string;
  status_from_date: string;
  status_to_date: string;
  status_properties_name: string;
  org_name: string;
  org_from_date: string;
  job_name: string;
  time_in_position: string;
  time_in_company: string;
  contract_name: string;
  contract_duration: string;
}


// used in card model
interface IDurationModel {
  years: number;
  months: number;
  days: number;
}

// used in job title
export interface IEmpJobStatusPositionModel {
  Person_ID: number;
  id: number;
  Status_ID: number;
  status: string;
  status_From_Date: string;
  reasons: string;
  orgId: number;
  organization: string;
  orgStartDate: string;
  jobId: number;
  job: string;
  startDate: string;
  timeInPosition: string;
  timeInCompany: string;
  counted: boolean;
  supervisorId: number;
  date: string;
}

// used in card model
export interface IPersonInstanceStatusModel {
  id: number;
  business_Entity_ID: number;
  calculate_by_Contract: boolean;
  company_ID: number;
  core_Status_Properties: ICoreStatusPropertiesModel;
  from_Date: string;
  person_Instance_ID: number;
  statusPropertiesId: number;
  status_ID: number;
  to_Date: string;
  core_Person_Instance: IPersonInstanceModel;
  core_Person_Instance_Org: IPersonInstanceOrg[];
  core_Status: IJobStatusModel;
}


// getList Model
export interface IEmpJobDataModel {
  id: number;
  person_Instance_ID: number;
  status_ID: number;
  from_Date: string;
  to_Date: string;
  statusPropertiesId: number;
  calculate_by_Contract: boolean;
  business_Entity_ID: number;
  company_ID: number;
  core_Person_Instance: IPersonInstanceModel;
  core_Status: IJobStatusModel;
  core_Status_Properties: ICoreStatusPropertiesModel;
  core_Person_Instance_Org: IPersonInstanceOrg[];
}


export interface IDeleteEmpStatusModel {
  employeeStatusID: number;
  employeeJobTitleID: number;
  DeleteStatusIfOrphan: boolean;
}

