import { Component, OnInit } from "@angular/core";
import { empPermissionDisplay } from "app/personnel-setup/mapper/emp-balances";
import {
  IEmpPermissionDisplayModel,
  IEmpPermissionModel,
} from "app/personnel-setup/models/emp-balances/permission";
import { EmployeePermissionService } from "app/personnel-setup/services/employees/employee-balances/employee-permission.service";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { EmpPermissionDialogComponent } from "../emp-permission-dialog/emp-permission-dialog.component";

@Component({
  selector: "emp-permission-card",
  templateUrl: "./emp-permission-card.component.html",
  styleUrls: ["./emp-permission-card.component.scss"],
})
export class EmpPermissionCardComponent extends AppHelpers implements OnInit {
  employeeInstanceId: number = 0;
  permissions: IEmpPermissionDisplayModel[];

  isLoaded: boolean = false;

  constructor(private employeePermissionService: EmployeePermissionService) {
    super();
    this.employeeInstanceId = EmpGeneralInfoService.EmployeeInstanceId;
    EmpGeneralInfoService.employeeInstanceId$.subscribe((empInstanceId) => {
      this.employeeInstanceId = empInstanceId;
      if (this.employeeInstanceId > 0 && this.checkNoBusinessOrCompany()) {
        this.isLoaded = false;
        this.getAll();
      }
    });
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    this.permissions = null;
    if (this.employeeInstanceId > 0 && this.checkNoBusinessOrCompany())
      this.getAll();
    else this.isLoaded = true;
  }

  getAll() {
    this.permissions = null;
    this.employeePermissionService
      .getAll(this.employeeInstanceId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IEmpPermissionModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          if (res.data.length > 5) {
            let list1: IEmpPermissionModel[] = [];
            let list2: IEmpPermissionModel[] = [];
            // general permission
            res.data.forEach((permission) => {
              if (this.isGeneralPermission(permission)) list1.push(permission);
              else list2.push(permission);
            });
            let sortedList: IEmpPermissionModel[] = [
              // sort by end date (from new to old) > without general
              ...list1,
              ...list2
                .sort(
                  (a, b) =>
                    new Date(b.closing_balance_date).getTime() -
                    new Date(a.closing_balance_date).getTime()
                )
                .slice(0, 5 - list1.length),
            ];
            // sort by end date (from new to old) > all list
            sortedList.sort(
              (a, b) =>
                new Date(b.closing_balance_date).getTime() -
                new Date(a.closing_balance_date).getTime()
            );
            this.permissions = empPermissionDisplay(sortedList).reverse();
          } else {
            let sortedList: IEmpPermissionModel[] = res.data;
            sortedList.sort(
              (a, b) =>
                new Date(b.closing_balance_date).getTime() -
                new Date(a.closing_balance_date).getTime()
            );
            this.permissions = empPermissionDisplay(sortedList).reverse();
          }
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  openAddEditDialog(): void {
    this.dialogsService
      .addEditDialog(EmpPermissionDialogComponent)
      .afterClosed()
      .subscribe((updated: boolean) => {
        if (updated) {
          this.isLoaded = false;
          this.getAll();
        }
      });
  }

  isGeneralPermission(permission: IEmpPermissionModel) {
    return (
      permission.permission_Setup_Name
        .toLocaleLowerCase()
        .includes("general") ||
      permission.permission_Setup_Name.toLocaleLowerCase().includes("gp")
    );
  }
}
