import {
  HelpersFunctions,
  msgTranslate,
} from "app/shared/resources/helpers-functions";
import { IEmpCustodyModel } from "../models/emp-employment-info/custodies";
import { IEmployeeWorkScheduleModel } from "../models/emp-employment-info/work-schedule";
import { IEmpSocialInsuranceModel } from "../models/emp-employment-info/social-insurance";
import {
  IEmpLocationDisplayModel,
  IEmpLocationModel,
} from "../models/emp-employment-info/emp-location";
import {
  IEmpHiringDocumentDisplayModel,
  IEmpHiringDocumentModel,
} from "../models/emp-employment-info/hiring-documents";
import { EEmpHireDocType } from "app/shared/enums/emp-hire-doc-type.enum";
import {
  IContractModel,
  IEmpContractDisplayModel,
} from "../models/emp-employment-info/contract";

export function empCustodyDisplay(
  data: IEmpCustodyModel[]
): IEmpCustodyModel[] {
  return data.map((custody) => {
    return <IEmpCustodyModel>{
      ...custody,
      custodies: msgTranslate(
        custody.core_Covenant?.ar_Name,
        custody.core_Covenant?.en_Name
      ),
      froM_DATE: HelpersFunctions.mapDate(custody.fromDate),
      endDate: HelpersFunctions.mapDate(custody.toDate),
      date:
        HelpersFunctions.mapDate(custody?.fromDate) +
        (custody?.toDate
          ? " - " + HelpersFunctions.mapDate(custody?.toDate)
          : ""),
    };
  });
}

export function empWorkScheduleDisplay(
  data: IEmployeeWorkScheduleModel[]
): IEmployeeWorkScheduleModel[] {
  return data.map((plan) => {
    return <IEmployeeWorkScheduleModel>{
      ...plan,
      plan: msgTranslate(plan.att_Plan?.ar_Name, plan.att_Plan?.en_Name),
      employee: msgTranslate(
        plan.core_Person_Instance?.core_Person?.ar_Name,
        plan.core_Person_Instance?.core_Person?.en_Name
      ),
      startDate: HelpersFunctions.mapDate(plan.fromDate),
      toDateOriginal: plan.toDate,
      toDate: HelpersFunctions.mapDate(plan.toDate),
    };
  });
}

export function empInsuranceOfficeDisplay(
  data: IEmpSocialInsuranceModel[]
): IEmpSocialInsuranceModel[] {
  return data.map((office) => {
    return <IEmpSocialInsuranceModel>{
      ...office,
      insuranceOffice: msgTranslate(
        office.core_Insurance_Office?.ar_Name,
        office.core_Insurance_Office?.en_Name
      ),
      fromDate: HelpersFunctions.mapDate(office.froM_DATE),
      endDate: HelpersFunctions.mapDate(office.enD_DATE),
      date:
        HelpersFunctions.mapDate(office?.froM_DATE) +
        (office?.enD_DATE
          ? " - " + HelpersFunctions.mapDate(office?.enD_DATE)
          : ""),
    };
  });
}

export function empLocationDisplay(
  data: IEmpLocationModel[]
): IEmpLocationDisplayModel[] {
  let locations: IEmpLocationDisplayModel[] = data.map((location) => {
    return <IEmpLocationDisplayModel>{
      ...location,
      country: msgTranslate(
        location.comm_Country?.ar_Name,
        location.comm_Country?.en_NAme
      ),
      governorate: msgTranslate(
        location.comm_Governorate?.ar_Name,
        location.comm_Governorate?.en_Name
      ),
      froM_DATE: HelpersFunctions.mapDate(location.from_Date),
      tO_DATE: HelpersFunctions.mapDate(location.to_Date),
    };
  });

  // to use properties from first map
  locations.map((location) => {
    location.allAddress = `${location.location ?? "-"}, ${
      location.governorate ?? "-"
    }`;
    location.date =
      location?.froM_DATE +
      (location?.to_Date ? " - " + location?.tO_DATE : "");
  });

  return locations;
}

export function empHiringDocumentDisplay(
  data: IEmpHiringDocumentModel[]
): IEmpHiringDocumentDisplayModel[] {
  return data.map((doc) => {
    return <IEmpHiringDocumentDisplayModel>{
      ...doc,
      documentName: msgTranslate(doc.ar_Name_Type, doc.en_Name_Type),
      attachmentType: EEmpHireDocType.getValue(doc.is_Attached),
      attachment: doc.attachmentFilePath ? "/" + doc.attachmentFilePath : "",
    };
  });
}

export function empContractDisplay(
  data: IContractModel[]
): IEmpContractDisplayModel[] {
  return data.map((contract) => {
    return <IEmpContractDisplayModel>{
      ...contract,
      contractType: msgTranslate(
        contract.core_Contract_Type.ar_Name,
        contract.core_Contract_Type.en_Name
      ),
      start_date: HelpersFunctions.mapDate(contract.starT_DATE),
      end_date: HelpersFunctions.mapDate(contract.enD_DATE),
      actual_End_Date: HelpersFunctions.mapDate(contract.actual_End_Date),
    };
  });
}
