import {IResponse, IResponseList} from "app/shared/interfaces/responces";
import {Component, OnInit} from "@angular/core";
import {FormGroup, Validators} from "@angular/forms";
import {MatTableDataSource} from "@angular/material/table";
import {
  EmployeeSocialInsuranceService
} from "app/personnel-setup/services/employees/employement-information/employee-social-insurance.service";
import {MessageTypes} from "app/shared/enums/message-types.enum";
import {AppHelpers} from "app/shared/resources/app-helpers";
import {CustomValidator} from "app/shared/validators/custom.validator";
import {EmpGeneralInfoService} from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import {ISaveEmpGeneralInfo} from "app/personnel-setup/models/employees";
import {MatDialogRef} from "@angular/material/dialog";
import {
  IEmpSocialInsuranceDelete,
  IEmpSocialInsuranceEditRequest,
  IEmpSocialInsuranceModel,
  IEmpSocialInsuranceRequest,
} from "app/personnel-setup/models/emp-employment-info/social-insurance";
import {ViewsManager} from "app/shared/resources/views-manager";
import {IPageParams} from "app/shared/interfaces/params";
import {HelpersFunctions} from "app/shared/resources/helpers-functions";
import {empInsuranceOfficeDisplay} from "app/personnel-setup/mapper/emp-employment";

@Component({
  selector: "emp-Social-insurance-dialog",
  templateUrl: "./emp-social-insurance-dialog.component.html",
  styleUrls: ["./emp-social-insurance-dialog.component.scss"],
})
export class EmpSocialInsuranceDialogComponent
  extends AppHelpers
  implements OnInit {
  employeeId: number = 0;
  // employeeInstanceId: number = 0;
  isHasUpdate: boolean;
  mandatoryColomns: string[] = [
    "Ser",
    // "id",
    "insuranceOffice",
    "fromDate",
    "endDate",
    "actions",
  ];
  optionalColomns: string[] = [];
  dataSource: MatTableDataSource<IEmpSocialInsuranceModel>;

  isLoaded: boolean = false;
  form: FormGroup;
  submitted: boolean = false;
  oldEmployeeData: {
    insurance_Office_ID: number;
    from_date: string;
  };

  constructor(
    private employeeSocialInsuranceService: EmployeeSocialInsuranceService,
    public dialogRef: MatDialogRef<EmpSocialInsuranceDialogComponent>
  ) {
    super();
    this.employeeId = EmpGeneralInfoService.EmployeeId;
    // this.employeeInstanceId = EmpGeneralInfoService.EmployeeInstanceId;

    this.rowFunctions = this.getSubRowFunctions(
      this.getParentViewName,
      ViewsManager.emp_social_insurance_sec
    );
  }

  ngOnInit(): void {
    this.form = this.fb.group(
      {
        id: [0, {nonNullable: true}],
        insurance_Office_ID: [null, [Validators.required]],
        fromDate: [null, [Validators.required]],
        endDate: [null],
        // worked_at: [null],
      },
      {
        validator: CustomValidator.compareDates("fromDate", "endDate"),
      }
    );

    this.dialogRef.backdropClick().subscribe((ele) => {
      this.onCloseDialog();
    });
  }

  get formCtrls() {
    return this.form.controls;
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany()) {
      if (this.employeeId > 0) this.getList(this.firstPage);
    }
  }

  onCloseDialog() {
    this.dialogRef.close(this.isHasUpdate);
  }

  getList(page: number, pageSize?: number) {
    this.page.pageIndex = page;
    if (pageSize) this.page.dialogSize = pageSize;
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.dialogSize,
    };
    this.employeeSocialInsuranceService
      .getList(this.employeeId, params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IEmpSocialInsuranceModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(
            empInsuranceOfficeDisplay(res.data)
          );
          this.page.length = res.meta.totalItemCount;
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IEmpSocialInsuranceRequest = {
        employeeData: {
          person_ID: this.employeeId,
          insurance_Office_ID: this.form.value.insurance_Office_ID,
          from_date: HelpersFunctions.mapDateReverse(this.form.value.fromDate),
          end_date: HelpersFunctions.mapDateReverse(this.form.value.endDate),
          // worked_at: this.form.value.worked_at,
        },
      };

      this.form.value.id == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IEmpSocialInsuranceRequest) {
    this.showLoading();
    this.employeeSocialInsuranceService
      .create(model)
      .subscribe((res: IResponse<IEmpSocialInsuranceModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getList(this.firstPage);
          this.isHasUpdate = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  update(model: IEmpSocialInsuranceRequest) {
    this.showLoading();
    let body: IEmpSocialInsuranceEditRequest = {
      employeeData: model.employeeData,
      oldEmployeeData: {
        person_ID: this.employeeId,
        ...this.oldEmployeeData,
      },
    };
    this.employeeSocialInsuranceService
      .update(body)
      .subscribe((res: IResponse<IEmpSocialInsuranceModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.clearForm();
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          this.getList(this.currentPage);
          this.oldEmployeeData = null;
          this.isHasUpdate = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  socialInsuranceToEdit(socialInsurance: IEmpSocialInsuranceModel) {
    this.form.patchValue(socialInsurance);
    this.oldEmployeeData = {
      insurance_Office_ID: socialInsurance.insurance_Office_ID,
      from_date: socialInsurance.froM_DATE,
    };
    this.formCtrls.fromDate.setValue(socialInsurance.froM_DATE);
    this.formCtrls.endDate.setValue(socialInsurance.enD_DATE);
    // to detect edit
    this.formCtrls.id.setValue(socialInsurance.core_Insurance_Office.id);
  }

  onDelete(socialInsurance: IEmpSocialInsuranceModel): void {
    let msg = socialInsurance.insuranceOffice;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(socialInsurance);
    });
  }

  delete(socialInsurance: IEmpSocialInsuranceModel) {
    this.showLoading();
    let params: IEmpSocialInsuranceDelete = {
      PersonID: this.employeeId,
      insuranceoffice: socialInsurance.insurance_Office_ID,
      fromdate: socialInsurance.froM_DATE,
    };
    this.employeeSocialInsuranceService
      .Delete(params)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          if (this.dataSource.data.length == 1 && this.currentPage > 1) {
            // if last item in list: get previous page
            this.getList(this.prevPage, this.page.dialogSize);
          } else {
            // if not last item in list: get current page
            this.getList(this.currentPage, this.page.dialogSize);
          }
          this.isHasUpdate = true;
          if (
            socialInsurance.froM_DATE == this.oldEmployeeData.from_date &&
            socialInsurance.insurance_Office_ID ==
            this.oldEmployeeData.insurance_Office_ID &&
            this.formCtrls.id.value > 0
          )
            this.clearForm();
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  onSaveInsuranceNumber(data: ISaveEmpGeneralInfo) {
    //  this.empGeneralInfoService.getEmployeeShared(this.employeeId);
    //this.dialogRef.close(data);
  }

  clearForm() {
    this.form.reset({id: 0});
    this.submitted = false;
  }
}
