import { Component, EventEmitter, OnInit, Output } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { empContractDisplay } from "app/personnel-setup/mapper/emp-employment";
import {
  IContractModel,
  IEmpContractDetailsRequest,
  IEmpContractDisplayModel,
} from "app/personnel-setup/models/emp-employment-info/contract";
import {
  EmployeeContractDetailsService
} from "app/personnel-setup/services/employees/employement-information/employee-contract-details.service";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { Idropdown } from "app/shared/interfaces/dropdown.interface";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { ViewsManager } from "app/shared/resources/views-manager";
import { CustomValidator } from "app/shared/validators/custom.validator";

@Component({
  selector: "emp-contract-section",
  templateUrl: "./emp-contract-section.component.html",
  styleUrls: ["./emp-contract-section.component.scss"],
})
export class EmpContractDetailsDialogComponent extends AppHelpers implements OnInit {
  employeeId: number = 0;
  @Output() changUpdated = new EventEmitter<boolean>();

  mandatoryColomns: string[] = [
    "Ser",
    // "id",
    "contractType",
    "contracT_NO",
    "start_date",
    "end_date",
    "autO_RENEWAL",
    "actions",
  ];
  optionalColomns: string[] = [
    "autO_Resign",
    "include_In_Previous_Duration",
    "actual_End_Date",
  ];
  dataSource: MatTableDataSource<IEmpContractDisplayModel>;

  isLoaded: boolean = false;

  form: FormGroup;
  submitted: boolean = false;

  autoRenewalList: Idropdown[] = [
    {
      id: "null",
      ar_Name: "غير محدد",
      en_Name: "Not set",
    },
    {
      id: "true",
      ar_Name: "صحيح",
      en_Name: "True",
    },
    {
      id: "false",
      ar_Name: "خطأ",
      en_Name: "False",
    },
  ];

  constructor(
    private employeeContractDetailsService: EmployeeContractDetailsService
  ) {
    super();
    this.employeeId = EmpGeneralInfoService.EmployeeId;

    this.rowFunctions = this.getSubRowFunctions(
      this.getParentViewName,
      ViewsManager.emp_job_info_contract_sec
    );
  }

  ngOnInit(): void {
    this.form = this.fb.group(
      {
        id: [0, {nonNullable: true}],
        contracT_TYPE_ID: [null, [Validators.required]],
        contracT_NO: [null, [Validators.required]],
        starT_DATE: [null, [Validators.required]],
        enD_DATE: [null, [Validators.required]],
        autO_RENEWAL: ["false"],
        autO_Resign: [null],
        include_In_Previous_Duration: [null],
      },
      {
        validator: CustomValidator.compareDates("starT_DATE", "enD_DATE"),
      }
    );
  }

  get formCtrls() {
    return this.form.controls;
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0) {
      this.dataSource = new MatTableDataSource(null);
    }
    if (this.checkNoBusinessOrCompany() && this.employeeId > 0) {
      this.getList(this.firstPage);
    }
  }

  getList(page: number, pageSize?: number) {
    this.dataSource = new MatTableDataSource(null);
    this.isLoaded = false;
    this.page.pageIndex = page;
    if (pageSize) {
      this.page.dialogSize = pageSize;
    }
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.dialogSize,
    };
    this.employeeContractDetailsService
      .getList(this.employeeId, params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IContractModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(
            empContractDisplay(res.data)
          );
          this.page.length = res.meta.totalItemCount;
          this.isLoaded = true;
          this.hideLoading();
        } else {
          this.errorList(res.message.messages);
        }
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IEmpContractDetailsRequest = {
        EmployeeData: {
          person_ID: this.employeeId,
          id: this.form.value.id,
          contracT_TYPE_ID: this.form.value.contracT_TYPE_ID,
          contracT_NO: this.form.value.contracT_NO,
          starT_DATE: HelpersFunctions.mapDateReverse(
            this.form.value.starT_DATE
          ),
          enD_DATE: HelpersFunctions.mapDateReverse(this.form.value.enD_DATE),
          autO_RENEWAL:
            this.form.value.autO_RENEWAL == "null"
              ? null
              : this.form.value.autO_RENEWAL == "false"
                ? false
                : true,
          autO_Resign: this.form.value.autO_Resign ?? false,
          include_In_Previous_Duration:
            this.form.value.include_In_Previous_Duration ?? false,
        },
      };

      model.EmployeeData.id == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IEmpContractDetailsRequest) {
    this.showLoading();
    this.employeeContractDetailsService
      .create(model)
      .subscribe((res: IResponse<IContractModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          // this.clearForm();
          this.formCtrls.id.setValue(res.data.id);
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getList(this.currentPage);
          this.isUpdated(true);
        } else {
          this.errorList(res.message.messages);
          this.hideLoading();
        }
      });
  }

  update(model: IEmpContractDetailsRequest) {
    this.showLoading();
    this.employeeContractDetailsService
      .update(model)
      .subscribe((res: IResponse<IContractModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.clearForm();
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          this.getList(this.currentPage);
          this.isUpdated(true);
        } else {
          this.errorList(res.message.messages);
          this.hideLoading();
        }
      });
  }

  employeeContractDetailsToEdit(contract: IEmpContractDisplayModel) {
    this.form.patchValue(contract);
    this.formCtrls.autO_RENEWAL.setValue(String(contract.autO_RENEWAL));
    // console.log(this.form.value);
  }

  onDelete(contract: IEmpContractDisplayModel): void {
    let msg = `${contract.contractType} -
      ${contract.start_date} -
      ${contract.end_date}`;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) {
        this.delete(contract);
      }
    });
  }

  delete(contract: IEmpContractDisplayModel) {
    this.showLoading();
    this.employeeContractDetailsService
      .Delete(contract.id)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          if (this.dataSource.data.length == 1 && this.currentPage > 1) {
            // if last item in list: get previous page
            this.getList(this.prevPage, this.page.dialogSize);
          } else {
            // if not last item in list: get current page
            this.getList(this.currentPage, this.page.dialogSize);
          }
          this.isUpdated(true);
          if (contract.id == this.formCtrls.id.value) {
            this.clearForm();
          }
        } else {
          this.errorList(res.message.messages);
          this.hideLoading();
        }
      });
  }

  clearForm() {
    this.form.reset({id: 0, autO_RENEWAL: "false"});
    this.submitted = false;
  }

  isUpdated(value: boolean) {
    this.changUpdated.emit(value);
  }
}
