import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatDialogRef } from "@angular/material/dialog";
import { MatTableDataSource } from "@angular/material/table";
import {
  IEmployeeDocumentDataModel,
  IEmployeeDocumentModel,
  IEmployeeDocumentRequest,
} from "app/personnel-setup/models/emp-personal-info/employee-documents";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { CustomValidators } from "ngx-custom-validators";
import { EmployeeDocumentsService } from "app/personnel-setup/services/employees/personal-info/employee-documents.service";
import { CustomValidator } from "app/shared/validators/custom.validator";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { employeePersonalDocumentToMap } from "app/personnel-setup/mapper/employee-personal-info";
import { ViewsManager } from "app/shared/resources/views-manager";
import { IPageParams } from "app/shared/interfaces/params";
import { IFileData } from "app/shared/interfaces/files";
import { IAttachIconColumnData } from "app/shared/components/resources/material-table/material-table.component";

@Component({
  selector: "app-employee-documents-dialog",
  templateUrl: "./emp-documents-dialog.component.html",
  styleUrls: ["./emp-documents-dialog.component.scss"],
})
export class EmployeeDocumentsDialogComponent
  extends AppHelpers
  implements OnInit
{
  employeeId: number = 0;
  updated: boolean = false;

  mandatoryColomns: string[] = [
    "Ser",
    // "id",
    "documentName",
    "dateof_issue",
    "expire_Date",
    "identity_Number",
    "attachment",
    "actions",
  ];
  optionalColomns: string[] = [];
  dataSource: MatTableDataSource<IEmployeeDocumentDataModel>;

  isLoaded: boolean = false;

  form: FormGroup;
  submitted: boolean = false;
  removeFiles: boolean = false;

  constructor(
    private employeeDocumentsService: EmployeeDocumentsService,
    public dialogRef: MatDialogRef<EmployeeDocumentsDialogComponent>
  ) {
    super();
    this.employeeId = EmpGeneralInfoService.EmployeeId;

    this.rowFunctions = this.getSubRowFunctions(
      this.getParentViewName,
      ViewsManager.emp_documents_sec
    );
  }

  ngOnInit(): void {
    this.form = this.fb.group(
      {
        id: [0, { nonNullable: true }],
        documentId: [null, [Validators.required]],
        issuingDate: [null, [Validators.required]],
        expirationDate: [null, [CustomValidators.date]],
        identity_Number: [null, [CustomValidators.digits]],
        file: [null],
      },
      {
        validator: CustomValidator.compareDates(
          "issuingDate",
          "expirationDate"
        ),
      }
    );

    this.dialogRef.backdropClick().subscribe((ele) => {
      this.onCloseDialog();
    });
  }

  get formCtrls() {
    return this.form.controls;
  }

  onCloseDialog() {
    this.dialogRef.close(this.updated);
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0) {
      this.dataSource = new MatTableDataSource(null);
    }
    if (this.checkNoBusinessOrCompany()) {
      if (this.employeeId > 0) this.getList(this.firstPage);
    }
  }

  getList(page: number, pageSize?: number) {
    this.page.pageIndex = page;
    if (pageSize) this.page.dialogSize = pageSize;
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.dialogSize,
    };
    this.employeeDocumentsService
      .getList(this.employeeId, params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IEmployeeDocumentModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(
            res.data?.length > 0 ? employeePersonalDocumentToMap(res.data) : []
          );
          this.page.length = res.meta.totalItemCount;
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    this.removeFiles = false;
    if (this.form.valid) {
      const model: IEmployeeDocumentRequest = {
        personID: this.employeeId,
        empIdentityID: this.form.value.id,
        identity_TypeID: this.form.value.documentId,
        dateof_issue: HelpersFunctions.mapDateReverse(
          this.form.value.issuingDate
        ),
        expire_Date:
          HelpersFunctions.mapDateReverse(this.form.value.expirationDate) ?? "",
        identity_Number: this.form.value.identity_Number ?? "",
        files: this.form.value.file,
      };

      const formData = HelpersFunctions.objectToFormData(model);

      model.empIdentityID == 0 ? this.create(formData) : this.update(formData);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: FormData) {
    this.showLoading();
    this.employeeDocumentsService
      .create(model)
      .subscribe((res: IResponse<IEmployeeDocumentModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getList(this.currentPage);
          this.updated = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  update(model: FormData) {
    // if file not change
    if (this.formCtrls.file.value?.id > 0) model.set("files", null);
    this.showLoading();
    this.employeeDocumentsService
      .update(model)
      .subscribe((res: IResponse<IEmployeeDocumentModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.clearForm();
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          this.getList(this.currentPage);
          this.updated = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  employeeDocumentToEdit(document: IEmployeeDocumentDataModel) {
    this.form.patchValue(document);
    this.formCtrls.id.setValue(document.empIdentityID);
    this.formCtrls.documentId.setValue(document.identity_TypeID);
    this.formCtrls.issuingDate.setValue(document.originalIssuingDate);
    this.formCtrls.expirationDate.setValue(document.originalExpirationDate);

    if (document.attachmentID) {
      // get file size && type
      this.showLoading();
      this.fileService
        .downloadByAttachId(document.attachmentID)
        .subscribe((res) => {
          let file: IFileData = {
            id: document.attachmentID,
            path: document.attachment,
            name: document.attachment_En_Name,
            type: res.type,
            size: res.size,
          };
          this.formCtrls.file.setValue(file);
          this.hideLoading();
        });
    } else this.formCtrls.file.setValue(null);

    // console.log(this.form.value);
  }

  onDelete(document: IEmployeeDocumentDataModel): void {
    let msg = this.msgTranslate(document.documentName);
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) {
        this.delete(document);
      }
    });
  }

  delete(document: IEmployeeDocumentDataModel) {
    this.showLoading();
    this.employeeDocumentsService
      .Delete(document.empIdentityID)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          if (this.dataSource.data.length == 1 && this.currentPage > 1) {
            // if last item in list: get previous page
            this.getList(this.prevPage, this.page.dialogSize);
          } else {
            // if not last item in list: get current page
            this.getList(this.currentPage, this.page.dialogSize);
          }
          this.updated = true;
          if (document.empIdentityID == this.formCtrls.id.value) {
            this.clearForm();
          }
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
    this.removeFiles = true;
  }

  attachIconColumnData: IAttachIconColumnData = {
    columnName: "attachment",
    icon: "fas fa-file-alt",
  };

  deleteFile() {
    this.showLoading();
    this.employeeDocumentsService
      .DeleteAttachment(this.formCtrls.id.value)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          this.getList(this.currentPage);
          this.formCtrls.file.reset();
        } else {
          this.errorList(res.message.messages);
          this.hideLoading();
        }
      });
  }
}
