import { Component, OnInit } from "@angular/core";
import { empContractDisplay } from "app/personnel-setup/mapper/emp-employment";
import {
  IContractModel,
  IEmpContractDisplayModel,
} from "app/personnel-setup/models/emp-employment-info/contract";
import {
  IEmpJobStatusPositionCardModel,
  IEmpJobStatusPositionModel,
  IEmpStatusCardDisplayModel
} from "app/personnel-setup/models/employee-employment-info";
import {
  EmployeeContractDetailsService
} from "app/personnel-setup/services/employees/employement-information/employee-contract-details.service";
import {
  EmployeeJobInformationService
} from "app/personnel-setup/services/employees/employement-information/employee-job-information.service";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { EmpJobInfoDialogComponent } from "./../emp-job-info-dialog/emp-job-info-dialog.component";
import { empStatusCardDisplay } from "app/personnel-setup/mapper/emp_status_position";

@Component({
  selector: "emp-job-info-card",
  templateUrl: "./emp-job-info-card.component.html",
  styleUrls: ["./emp-job-info-card.component.scss"],
})
export class EmpJobInfoCardComponent extends AppHelpers implements OnInit {
  employeeId: number = 0;
  employeeInstanceId: number = 0;
  job: IEmpStatusCardDisplayModel;
  isLoaded: boolean = false;

  constructor(
    private employeeJobInformationService: EmployeeJobInformationService,
    // private employeeContractDetailsService: EmployeeContractDetailsService
  ) {
    super();
    this.employeeId = EmpGeneralInfoService.EmployeeId;
    this.employeeInstanceId = EmpGeneralInfoService.EmployeeInstanceId;
    EmpGeneralInfoService.employeeInstanceId$.subscribe((empInstanceId) => {
      this.employeeInstanceId = empInstanceId;
      if (this.employeeInstanceId > 0 && this.checkNoBusinessOrCompany()) {
        this.isLoaded = false;
        this.getStatusPositionCard();
      }
    });
  }

  ngOnInit(): void { }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) {
      if (this.employeeInstanceId > 0) {
        this.getStatusPositionCard();
      } else {
        this.isLoaded = true;
      }
    }
  }

  getStatusPositionCard() {
    return this.employeeJobInformationService
      .getStatusPositionCard(this.employeeInstanceId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IEmpJobStatusPositionCardModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.isLoaded = true;
          this.job = empStatusCardDisplay(res.data);
          //  console.log("getStatusPositionCard", res.data);
        } else {
          this.errorList(res.message.messages);
        }
      });
  }


  openAddEditDialog(): void {
    this.dialogsService
      .addEditDialog(EmpJobInfoDialogComponent)
      .afterClosed()
      .subscribe(
        (data: { isJobUpdated: boolean; isContractUpdated: boolean }) => {
          if (data.isJobUpdated) {
            this.isLoaded = false;
            this.getStatusPositionCard();
          }
        }
      );
  }
}
