import {Component, OnInit} from "@angular/core";
import {MessageTypes} from "app/shared/enums/message-types.enum";
import {IResponse, IResponseMsg} from "app/shared/interfaces/responces";
import {AppHelpers} from "app/shared/resources/app-helpers";
import {EmpWorkScheduleDialogComponent} from "../emp-work-schedule-dialog/emp-work-schedule-dialog.component";
import {
  EmployeeWorkScheduleService
} from "app/personnel-setup/services/employees/employement-information/employee-work-schedule.service";
import {
  IEmpPlanCardDayModel,
  IEmpWorkScheduleCardModel,
} from "app/personnel-setup/models/emp-employment-info/work-schedule";
import {EmpGeneralInfoService} from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import {HelpersFunctions, msgTranslate} from "app/shared/resources/helpers-functions";

@Component({
  selector: "emp-work-schedule-card",
  templateUrl: "./emp-work-schedule-card.component.html",
  styleUrls: ["./emp-work-schedule-card.component.scss"],
})
export class EmpWorkScheduleCardComponent extends AppHelpers implements OnInit {
  employeeInstanceId: number;
  empWorkSchedule: IEmpWorkScheduleCardModel;
  isLoaded: boolean = false;
  today: Date = new Date();
  numberOfDaysInMonth: number;
  calendarArray: IEmpPlanCardDayModel[] = [];
  messages: IResponseMsg[]

  constructor(
    private employeeWorkScheduleService: EmployeeWorkScheduleService
  ) {
    super();
    // this.employeeId = EmpGeneralInfoService.EmployeeId;
    this.employeeInstanceId = EmpGeneralInfoService.EmployeeInstanceId;

    EmpGeneralInfoService.employeeInstanceId$.subscribe((empInstanceId) => {
      this.employeeInstanceId = empInstanceId;
      if (this.employeeInstanceId > 0 && this.checkNoBusinessOrCompany()) {
        this.isLoaded = false;
        this.getCardData();
      }
    });

  }

  ngOnInit(): void {
    this.numberOfDaysInMonth = HelpersFunctions.numberOfDaysInMonth(
      this.today.getFullYear(),
      // return current month (0-11)
      this.today.getMonth()
    );
  }

  override onChangeBusinessAndCompanyId(): void {
    this.empWorkSchedule = null;
    if (this.employeeInstanceId > 0 && this.checkNoBusinessOrCompany())
      this.getCardData();
    else this.isLoaded = true;
  }

  getCardData() {
    this.empWorkSchedule = null;
    this.employeeWorkScheduleService
      .getCard(this.employeeInstanceId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IEmpWorkScheduleCardModel>) => {
        this.isLoaded = true;
        if (res.message.messageType == MessageTypes.Success) {
          res.data.currentPlan.start_Date = HelpersFunctions.mapDate(
            res.data.currentPlan.start_Date
          );

          res.data.currentPlan.end_Date = HelpersFunctions.mapDate(
            res.data.currentPlan.end_Date
          );
          this.empWorkSchedule = res.data;
          this.createCalendarArray();
        }
        if (res.message.messageType == MessageTypes.Warning) {
          this.messages = res.message.messages
        } else this.errorList(res.message.messages);
      });
  }

  createCalendarArray() {
    this.calendarArray = [];
    // calendar array
    for (let i = 1; i <= this.numberOfDaysInMonth; i++) {

      let isWeekend = this.empWorkSchedule?.weekEnds
        .map((date) => new Date(date).getDate())
        .includes(i);

      let isVacation = this.empWorkSchedule?.vacations
        .map((vac) => new Date(vac.vacation_Date_Duration.vacation_date).getDate())
        .includes(i);

      let vacation = this.empWorkSchedule?.vacations
        .find((vac) => new Date(vac.vacation_Date_Duration.vacation_date).getDate() === i)?.vacation;

      let date = new Date();
      date.setDate(i)


      this.calendarArray.push({
        value: i,
        isCurrentDay: this.today.getDate() == i,
        isWeekend: isWeekend,
        isVacation: isVacation,
        hoverContent: "",
        date: date,
        vacation: msgTranslate(vacation?.ar_Name, vacation?.en_Name) ?? null
      });
    }
  }

  getDayPopOver(day: IEmpPlanCardDayModel): string {
    if (day.isWeekend) return `${HelpersFunctions.getDayName(day.date)} - ${this.translate("weekend")}`
    if (day.isVacation) return `${HelpersFunctions.getDayName(day.date)} - ${day.vacation}`;
    else return HelpersFunctions.getDayName(day.date);

  }

  openAddEditDialog(): void {
    this.dialogsService
      .addEditDialog(EmpWorkScheduleDialogComponent)
      .afterClosed()
      .subscribe((updated: boolean) => {
        if (updated) {
          this.isLoaded = false;
          this.getCardData();
        }
      });
  }
}
