import {Component, OnInit} from "@angular/core";
import {FormGroup, Validators} from "@angular/forms";
import {MatDialogRef} from "@angular/material/dialog";
import {MatTableDataSource} from "@angular/material/table";
import {empCareerDisplay} from "app/personnel-setup/mapper/emp-career";
import {
  IEmpWorkExperienceDisplay,
  IEmpWorkExperienceModel,
  IEmpWorkExperienceRequest,
} from "app/personnel-setup/models/employee-career/emp-work-experience";
import {EmpGeneralInfoService} from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import {EmployeeWorkExperienceService} from "app/personnel-setup/services/employees/career/employee-work-experience.service";
import {MessageTypes} from "app/shared/enums/message-types.enum";
import {IResponse, IResponseList} from "app/shared/interfaces/responces";
import {AppHelpers} from "app/shared/resources/app-helpers";
import {ViewsManager} from "app/shared/resources/views-manager";
import {CustomValidator} from "app/shared/validators/custom.validator";
import {CustomValidators} from "ngx-custom-validators";
import {IPageParams} from "app/shared/interfaces/params";
import {HelpersFunctions} from "app/shared/resources/helpers-functions";

@Component({
  selector: "app-employee-work-experience-dialog",
  templateUrl: "./employee-work-experience-dialog.component.html",
  styleUrls: ["./employee-work-experience-dialog.component.scss"],
})
export class EmployeeWorkExperienceDialogComponent extends AppHelpers implements OnInit {
  employeeId: number = 0;
  employeeInstanceId: number = 0;
  isHaseChange: boolean = false;

  mandatoryColomns: string[] = [
    "Ser",
    "companyName",
    "department",
    "position",
    "startDate",
    "endDate",
    "actions",
  ];
  optionalColomns: string[] = [];
  dataSource: MatTableDataSource<IEmpWorkExperienceDisplay>;
  isLoaded: boolean = false;
  form: FormGroup;
  submitted: boolean = false;

  constructor(
    private empWorkExperienceService: EmployeeWorkExperienceService,
    public dialogRef: MatDialogRef<EmployeeWorkExperienceDialogComponent>
  ) {
    super();
    this.employeeId = EmpGeneralInfoService.EmployeeId;
    this.employeeInstanceId = EmpGeneralInfoService.EmployeeInstanceId;
    this.rowFunctions = this.getSubRowFunctions(
      this.getParentViewName,
      ViewsManager.emp_work_experience_sec
    );
  }

  ngOnInit(): void {
    this.form = this.fb.group(
      {
        id: [0, {nonNullable: true}],
        companyName: [null, [Validators.required, CustomValidator.whiteSpace]],
        department: [null],
        position: [null],
        startDate: [null, [Validators.required, CustomValidators.date]],
        endDate: [null, [Validators.required, CustomValidators.date]],
      },
      {
        validator: CustomValidator.compareDates("startDate", "endDate"),
      }
    );

    this.dialogRef.backdropClick().subscribe((ele) => {
      this.onCloseDialog();
    });
  }

  get formCtrls() {
    return this.form.controls;
  }

  onCloseDialog() {
    this.dialogRef.close(this.isHaseChange);
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany()) {
      if (this.employeeId > 0) this.getList(this.firstPage);
    }
  }

  getList(page: number, pageSize?: number) {
    this.page.pageIndex = page;
    if (pageSize) this.page.dialogSize = pageSize;
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.dialogSize,
    };
    this.empWorkExperienceService
      .getList(this.employeeId, params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IEmpWorkExperienceModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(empCareerDisplay(res.data));
          this.page.length = res.meta.totalItemCount
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IEmpWorkExperienceRequest = {
        WorkExperience: {
          person_Instance_ID: this.employeeId,
          id: this.form.value.id,
          companyName: this.form.value.companyName,
          org_Name: this.form.value.department,
          job_Title_Name: this.form.value.position,
          from_Date: HelpersFunctions.mapDateReverse(this.form.value.startDate),
          to_Date: HelpersFunctions.mapDateReverse(this.form.value.endDate),
        }
      };

      model.WorkExperience.id == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IEmpWorkExperienceRequest) {
    this.showLoading();
    this.empWorkExperienceService
      .create(model)
      .subscribe((res: IResponse<IEmpWorkExperienceModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getList(this.firstPage);
          this.isHaseChange = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  update(model: IEmpWorkExperienceRequest) {
    this.showLoading();
    this.empWorkExperienceService
      .update(model)
      .subscribe((res: IResponse<IEmpWorkExperienceModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.clearForm();
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          this.getList(this.currentPage);
          this.isHaseChange = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  employeeWorkExperienceToEdit(empWork: IEmpWorkExperienceDisplay) {
    this.form.patchValue(empWork);
    this.formCtrls.startDate.setValue(empWork.from_Date)
    this.formCtrls.endDate.setValue(empWork.to_Date)
    console.log('empWork', empWork);
    console.log('form', this.form.value);
  }

  onDelete(empWork: IEmpWorkExperienceDisplay): void {
    let msg = this.msgTranslate(empWork.companyName);
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(empWork);
    });
  }

  delete(empWork: IEmpWorkExperienceDisplay) {
    this.showLoading();
    this.empWorkExperienceService.Delete(empWork.id).subscribe((res: IResponse) => {
      if (res.message.messageType == MessageTypes.Success) {
        this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
        if (this.dataSource.data.length == 1 && this.currentPage > 1) {
          // if last item in list: get previous page
          this.getList(this.prevPage, this.page.dialogSize);
        } else {
          // if not last item in list: get current page
          this.getList(this.currentPage, this.page.dialogSize);
        }
        this.isHaseChange = true;
        if (empWork.id == this.formCtrls.id.value) this.clearForm();
      } else this.errorList(res.message.messages);
      this.hideLoading();
    });
  }

  clearForm() {
    this.form.reset({id: 0});
    this.submitted = false;
  }
}
