import {Component, OnInit} from "@angular/core";
import {FormGroup, Validators} from "@angular/forms";
import {MatDialogRef} from "@angular/material/dialog";
import {MatTableDataSource} from "@angular/material/table";
import {employeeMaritalStatusToMap} from "app/personnel-setup/mapper/employee-personal-info";
import {
  IEditEmployeeMaritalRequest,
  IEmployeeMaritalDeleteParams,
  IEmployeeMaritalModel,
  IEmployeeMaritalRequest,
} from "app/personnel-setup/models/emp-personal-info/marital-status";
import {ISaveEmpGeneralInfo} from "app/personnel-setup/models/employees";
import {EmpGeneralInfoService} from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import {
  EmployeeMaritalDataService
} from "app/personnel-setup/services/employees/personal-info/employee-marital-data.service";
import {MessageTypes} from "app/shared/enums/message-types.enum";
import {IResponse} from "app/shared/interfaces/responces";
import {AppHelpers} from "app/shared/resources/app-helpers";
import {HelpersFunctions} from "app/shared/resources/helpers-functions";
import {ViewsManager} from "app/shared/resources/views-manager";

@Component({
  selector: "app-employee-personal-info-dialog",
  templateUrl: "./emp-personal-info-dialog.component.html",
  styleUrls: ["./emp-personal-info-dialog.component.scss"],
})
export class EmployeePersonalInfoDialogComponent
  extends AppHelpers
  implements OnInit
{
  employeeId: number = 0;
  updated: boolean = false;
  mandatoryColomns: string[] = [
    "Ser",
    // "id",
    "maritalStatus",
    "statusDate",
    "actions",
  ];
  optionalColomns: string[] = [];
  dataSource: MatTableDataSource<IEmployeeMaritalModel>;
  isLoaded: boolean = false;
  form: FormGroup;
  submitted: boolean = false;

  selectedMaritalId: number = 0;

  // @ViewChild(EmployeePersonalInfoCardComponent, { static: true })
  // personalInfoCard: EmployeePersonalInfoCardComponent;

  constructor(
    private employeeMaritalDataService: EmployeeMaritalDataService,
    public dialogRef: MatDialogRef<EmployeePersonalInfoDialogComponent>
  ) {
    super();
    this.employeeId = EmpGeneralInfoService.EmployeeId;

    // marital status index
    this.rowFunctions = this.getSubRowFunctions(
      this.getParentViewName,
      ViewsManager.emp_personal_info_sec
    );
  }

  ngOnInit(): void {
    this.form = this.fb.group({
      id: [0, { nonNullable: true }],
      maritalID: [null, [Validators.required]],
      statusDate: [null],
    });

    this.dialogRef.backdropClick().subscribe((ele) => {
      this.onCloseDialog();
    });
  }

  onCloseDialog() {
    this.dialogRef.close(this.updated);
  }

  get formCtrls() {
    return this.form.controls;
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.employeeId > 0 && this.checkNoBusinessOrCompany()) {
      this.getAllMaritalStatus();
    } else this.isLoaded = true;
  }

  getAllMaritalStatus() {
    this.employeeMaritalDataService
      .getAll(this.employeeId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IEmployeeMaritalModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(
            employeeMaritalStatusToMap(res.data)
          );
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  submit() {
    // console.log(this.personalInfoCard);
    // if (this.personalInfoCard) this.personalInfoCard?.getEmpAll_Military();

    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IEmployeeMaritalRequest = {
        EmployeeData: {
          personID: this.employeeId,
          maritalID: this.formCtrls.maritalID.value,
          statusDate: HelpersFunctions.mapDateReverse(
            this.formCtrls.statusDate.value
          ),
        },
      };

      this.formCtrls.id.value == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IEmployeeMaritalRequest) {
    this.showLoading();
    this.employeeMaritalDataService
      .create(model)
      .subscribe((res: IResponse<IEmployeeMaritalModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAllMaritalStatus();
          this.updated = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  update(model: IEmployeeMaritalRequest) {
    let editModel: IEditEmployeeMaritalRequest = {
      ...model,
      OriginalEmployeeData: {
        PersonID: this.employeeId,
        MaritalID: this.selectedMaritalId,
      },
    };
    this.showLoading();
    this.employeeMaritalDataService
      .update(editModel)
      .subscribe((res: IResponse<IEmployeeMaritalModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.clearForm();
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAllMaritalStatus();
          this.updated = true;
          this.selectedMaritalId = 0;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  employeeMaritalToEdit(employeeMaritalInfo: IEmployeeMaritalModel) {
    this.form.patchValue(employeeMaritalInfo);
    this.formCtrls.statusDate.setValue(employeeMaritalInfo.originalDateFormat);
    // not real id >> only to detect edit mode
    this.formCtrls.id.setValue(
      employeeMaritalInfo.comm_Marital_Status?.id ?? 1
    );

    // to use in edit request
    this.selectedMaritalId = employeeMaritalInfo.maritalID;
  }

  onDelete(employeeMaritalInfo: IEmployeeMaritalModel): void {
    let msg = this.msgTranslate(
      employeeMaritalInfo.comm_Marital_Status?.ar_Name,
      employeeMaritalInfo.comm_Marital_Status?.en_Name
    );
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(employeeMaritalInfo);
    });
  }

  delete(employeeMaritalInfo: IEmployeeMaritalModel) {
    let deleteParams: IEmployeeMaritalDeleteParams = {
      PersonID: this.employeeId,
      MaritalID: employeeMaritalInfo.comm_Marital_Status?.id,
    };
    this.showLoading();
    this.employeeMaritalDataService
      .Delete(deleteParams)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAllMaritalStatus();
          this.updated = true;
          if (
            employeeMaritalInfo.comm_Marital_Status?.id ==
              this.selectedMaritalId &&
            // if edit
            this.formCtrls.id.value > 0
          )
            this.clearForm();
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
  }

  onSave(data: ISaveEmpGeneralInfo) {
    // this.empGeneralInfoService.getEmployee(this.data.employeeId);
    // this.dialogRef.close(data);
  }

  changUpdated(value: boolean) {
    // this.dialogRef.close({ isMarital: true });
  }
}
