import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatDialogRef } from "@angular/material/dialog";
import { MatTableDataSource } from "@angular/material/table";
import { empS4CostCenterDisplay } from "app/personnel-setup/mapper/emp-sap-integration";
import {
  IEmployeeS4HanaModel,
  IEmployeeS4HanaRequest,
  IS4CostCenter,
} from "app/personnel-setup/models/emp-sap-integration/s4-hana";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import { EmployeeS4HanaService } from "app/personnel-setup/services/employees/sap-integration/employee-s4-hana.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { ViewsManager } from "app/shared/resources/views-manager";

@Component({
  selector: "app-emp-s4-hana-dialog",
  templateUrl: "./emp-s4-hana-dialog.component.html",
  styleUrls: ["./emp-s4-hana-dialog.component.scss"],
})
export class EmpS4HanaDialogComponent extends AppHelpers implements OnInit {
  // employeeId: number = 0;
  employeeInstanceId: number = 0;
  updated: boolean = false;

  mandatoryColomns: string[] = [
    "Ser",
    // "id",
    "s4SystemInstance",
    "costCenter",
    "costCenter_Text",
    "actions",
  ];
  optionalColomns: string[] = [];
  dataSource: MatTableDataSource<IEmployeeS4HanaModel>;

  isLoaded: boolean = false;

  form: FormGroup;
  submitted: boolean = false;

  constructor(
    private employeeS4HanaService: EmployeeS4HanaService,
    public dialogRef: MatDialogRef<EmpS4HanaDialogComponent>
  ) {
    super();
    // this.employeeId = EmpGeneralInfoService.EmployeeId;
    this.employeeInstanceId = EmpGeneralInfoService.EmployeeInstanceId;

    this.rowFunctions = this.getSubRowFunctions(
      this.getParentViewName,
      ViewsManager.emp_S4Hana_sec
    );
  }

  ngOnInit(): void {
    this.form = this.fb.group({
      id: [0, { nonNullable: true }],
      systemInstance_ID: [null, [Validators.required]],
      costCenter: [null, [Validators.required]],
      costCenter_Text: [null/* , [Validators.required] */],
    });

    this.dialogRef.backdropClick().subscribe((ele) => {
      this.onCloseDialog();
    });
  }

  get formCtrls() {
    return this.form.controls;
  }

  onCloseDialog() {
    this.dialogRef.close(this.updated);
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany()) {
      if (this.employeeInstanceId > 0) this.getList(this.firstPage);
    }
  }

  getList(page: number, pageSize?: number) {
    this.page.pageIndex = page;
    if (pageSize) this.page.dialogSize = pageSize;
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.dialogSize,
    };
    this.employeeS4HanaService
      .getList(this.employeeInstanceId, params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IEmployeeS4HanaModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(
            empS4CostCenterDisplay(res.data)
          );
          this.page.length = res.meta.totalItemCount;
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IEmployeeS4HanaRequest = {
        EmployeeData: {
          Person_Instance_ID: this.employeeInstanceId,
          ID: this.form.value.id,
          SystemInstance_ID: this.form.value.systemInstance_ID,
          CostCenter: this.form.value.costCenter,
          CostCenter_Text: this.form.value.costCenter_Text,
        },
      };

      model.EmployeeData.ID == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IEmployeeS4HanaRequest) {
    this.showLoading();
    this.employeeS4HanaService
      .create(model)
      .subscribe((res: IResponse<IEmployeeS4HanaModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getList(this.currentPage);
          this.updated = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  update(model: IEmployeeS4HanaRequest) {
    this.showLoading();
    this.employeeS4HanaService
      .update(model)
      .subscribe((res: IResponse<IEmployeeS4HanaModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.clearForm();
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          this.getList(this.currentPage);
          this.updated = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  employeeS4HanaToEdit(employeeS4HanaInfo: IEmployeeS4HanaModel) {
    this.form.patchValue(employeeS4HanaInfo);
    // console.log(this.form.value);
  }

  onDelete(employeeS4HanaInfo: IEmployeeS4HanaModel): void {
    let msg = employeeS4HanaInfo.s4SystemInstance;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(employeeS4HanaInfo);
    });
  }

  delete(employeeS4HanaInfo: IEmployeeS4HanaModel) {
    this.showLoading();
    this.employeeS4HanaService
      .Delete(employeeS4HanaInfo.id)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          if (this.dataSource.data.length == 1 && this.currentPage > 1) {
            // if last item in list: get previous page
            this.getList(this.prevPage, this.page.dialogSize);
          } else {
            // if not last item in list: get current page
            this.getList(this.currentPage, this.page.dialogSize);
          }
          this.updated = true;
          if (employeeS4HanaInfo.id == this.formCtrls.id.value)
            this.clearForm();
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
  }

  changeCostCenterText(costCenters: IS4CostCenter[]) {
    this.formCtrls.costCenter_Text.setValue(
      costCenters[0]?.costCenter_Text ?? null
    );
  }
}
