import {HelpersFunctions} from "app/shared/resources/helpers-functions";
import {Component, OnInit} from "@angular/core";
import {FormGroup, Validators} from "@angular/forms";
import {MatDialogRef} from "@angular/material/dialog";
import {MatTableDataSource} from "@angular/material/table";
import {
  IEditEmployeeBankingAccountRequest,
  IEmployeeBankingAccountDeleteParams,
  IEmployeeBankingAccountModel,
  IEmployeeBankingAccountRequest,
} from "app/personnel-setup/models/payments-information/emp-bank-account";
import {EmpGeneralInfoService} from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import {
  EmployeeBankingAccountService
} from "app/personnel-setup/services/employees/payments-info/employee-banking-account.service";
import {MessageTypes} from "app/shared/enums/message-types.enum";
import {IResponse, IResponseList} from "app/shared/interfaces/responces";
import {AppHelpers} from "app/shared/resources/app-helpers";
import {IPageParams} from "app/shared/interfaces/params";
import {CustomValidators} from "ngx-custom-validators";
import {empBankingAccountDisplay} from "app/personnel-setup/mapper/emp-payments-info";
import {ViewsManager} from "app/shared/resources/views-manager";

@Component({
  selector: "app-emp-banking-account-data-dialog",
  templateUrl: "./emp-banking-account-data-dialog.component.html",
  styleUrls: ["./emp-banking-account-data-dialog.component.scss"],
})
export class EmpBankingAccountDataDialogComponent
  extends AppHelpers
  implements OnInit {
  employeeInstanceId: number = 0;
  isHaseUpdate: boolean = false;

  mandatoryColomns: string[] = [
    "Ser",
    "bank",
    "accountNo",
    "startDate",
    "actions",
  ];
  optionalColomns: string[] = [];
  dataSource: MatTableDataSource<IEmployeeBankingAccountModel>;

  isLoaded: boolean = false;
  form: FormGroup;
  submitted: boolean = false;

  selectedAccountBank: IEmployeeBankingAccountModel;

  constructor(
    private empBankingService: EmployeeBankingAccountService,
    public dialogRef: MatDialogRef<EmpBankingAccountDataDialogComponent>
  ) {
    super();
    //  this.employeeId = EmpGeneralInfoService.EmployeeId;
    this.employeeInstanceId = EmpGeneralInfoService.EmployeeInstanceId;
    // this.form?.get('statusDate').setValue()

    this.rowFunctions = this.getSubRowFunctions(
      this.getParentViewName,
      ViewsManager.emp_banking_account_data_sec
    );
  }

  ngOnInit(): void {
    this.form = this.fb.group(
      {
        id: [0, {nonNullable: true}],
        bank_ID: [null, [Validators.required]],
        accountNo: [null, [Validators.required, CustomValidators.number]],
        startDate: [null, [Validators.required, CustomValidators.date]],
        cardNumber: [null], // not used in screen yet value (null)
        isDebit: [false], // not used in screen yet value (null)
        branchId: [null], // not used in screen yet value (null)
      },
    );

    this.dialogRef.backdropClick().subscribe((ele) => {
      this.onCloseDialog();
    });
  }

  get formCtrls() {
    return this.form.controls;
  }

  onCloseDialog() {
    this.dialogRef.close(this.isHaseUpdate);
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany()) {
      if (this.employeeInstanceId > 0) this.getList(this.firstPage);
    }
  }

  getList(page: number, pageSize?: number) {
    this.page.pageIndex = page;
    if (pageSize) this.page.dialogSize = pageSize;
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.dialogSize,
    };
    this.empBankingService
      .getList(this.employeeInstanceId, params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IEmployeeBankingAccountModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(
            empBankingAccountDisplay(res.data)
          );
          this.page.length = res.meta.totalItemCount;
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IEmployeeBankingAccountRequest = {
        employeeData: {
          person_Instance_ID: this.employeeInstanceId,
          bank_ID: this.form.value.bank_ID,
          accountNo: this.form.value.accountNo,
          from_Date: HelpersFunctions.mapDateReverse(this.form.value.startDate),
          cardNumber: this.form.value.cardNumber,
          isDebit: this.form.value.isDebit ?? false,
          branchId: this.form.value.branchId,
        },
      };

      this.formCtrls.id.value == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IEmployeeBankingAccountRequest) {
    this.showLoading();
    this.empBankingService
      .create(model)
      .subscribe((res: IResponse<IEmployeeBankingAccountModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getList(this.currentPage);
          this.isHaseUpdate = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  update(model: IEmployeeBankingAccountRequest) {
    let editModel: IEditEmployeeBankingAccountRequest = {
      ...model,
      OldEmployeeData: {
        person_Instance_ID: this.employeeInstanceId,
        bank_ID: this.selectedAccountBank.bank_ID,
        accountNo: this.selectedAccountBank.accountNo,
      },
    };
    this.showLoading();
    this.empBankingService
      .update(editModel)
      .subscribe((res: IResponse<IEmployeeBankingAccountModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.clearForm();
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          this.getList(this.currentPage);
          this.isHaseUpdate = true;
          this.selectedAccountBank = null;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  employeeBankingAccountToEdit(
    employeeBankingAccountInfo: IEmployeeBankingAccountModel
  ) {
    this.form.patchValue(employeeBankingAccountInfo);
    this.formCtrls.startDate.setValue(employeeBankingAccountInfo.from_Date);
    // to detect edit mode
    this.formCtrls.id.setValue(employeeBankingAccountInfo.bank_ID);
    this.selectedAccountBank = employeeBankingAccountInfo;
  }

  onDelete(employeeBankingAccountInfo: IEmployeeBankingAccountModel): void {
    let msg = employeeBankingAccountInfo.bank;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(employeeBankingAccountInfo);
    });
  }

  delete(employeeBankingAccountInfo: IEmployeeBankingAccountModel) {
    let deleteParams: IEmployeeBankingAccountDeleteParams = {
      Person_Instance_ID: this.employeeInstanceId,
      Bank_ID: employeeBankingAccountInfo.bank_ID,
      AccountNum: employeeBankingAccountInfo.accountNo,
    };
    this.showLoading();
    this.empBankingService.Delete(deleteParams).subscribe((res: IResponse) => {
      if (res.message.messageType == MessageTypes.Success) {
        this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
        if (this.dataSource.data.length == 1 && this.currentPage > 1) {
          // if last item in list: get previous page
          this.getList(this.prevPage, this.page.dialogSize);
        } else {
          // if not last item in list: get current page
          this.getList(this.currentPage, this.page.dialogSize);
        }
        this.isHaseUpdate = true;
        if (
          employeeBankingAccountInfo.comm_Bank?.id ==
          this.selectedAccountBank?.bank_ID &&
          employeeBankingAccountInfo.accountNo ==
          this.selectedAccountBank?.accountNo &&
          // if edit
          this.formCtrls.id.value > 0
        )
          this.clearForm();
      } else this.errorList(res.message.messages);
      this.hideLoading();
    });
  }

  clearForm() {
    //  this.form.reset({id: 0, statusDate: this.statusDate});
    this.submitted = false;
  }
}
