export enum EEmpHireDocType {
  attached = 1,
  handDelivery = 2,
  notNeed = 3,
}

export namespace EEmpHireDocType {
  export function getValue(docType: EEmpHireDocType | number): string {
    switch (docType) {
      case EEmpHireDocType.attached:
        return "attachment";
      case EEmpHireDocType.handDelivery:
        return "isHandDelivered";
      case EEmpHireDocType.notNeed:
        return "notNeed";
      default:
        return "";
    }
  }
}
