import {Component, OnInit} from "@angular/core";
import {IEmpWorkExperienceModel} from "app/personnel-setup/models/employee-career/emp-work-experience";
import {MessageTypes} from "app/shared/enums/message-types.enum";
import {IResponse} from "app/shared/interfaces/responces";
import {AppHelpers} from "app/shared/resources/app-helpers";
import {
  EmployeeWorkExperienceService
} from "app/personnel-setup/services/employees/career/employee-work-experience.service";
import {
  EmployeeWorkExperienceDialogComponent
} from "../emp-work-experience-dialog/employee-work-experience-dialog.component";
import {EmpGeneralInfoService} from "app/personnel-setup/services/employees/general-info/emp-general-info.service";

@Component({
  selector: "emp-work-experience-card",
  templateUrl: "./emp-work-experience-card.component.html",
  styleUrls: ["./emp-work-experience-card.component.scss"],
})
export class EmployeeWorkExperienceCardComponent extends AppHelpers implements OnInit {
  employeeInstanceId: number = 0;
  empWorkExperience: IEmpWorkExperienceModel;

  isLoaded: boolean = false;

  constructor(private employeeWorkExperienceService: EmployeeWorkExperienceService) {
    super();
    this.employeeInstanceId = EmpGeneralInfoService.EmployeeInstanceId;
    EmpGeneralInfoService.employeeInstanceId$.subscribe((empInstanceId) => {
      this.employeeInstanceId = empInstanceId;
      if (this.employeeInstanceId > 0 && this.checkNoBusinessOrCompany()) {
        this.isLoaded = false;
        this.getAll();
      }
    });
  }

  ngOnInit(): void {
  }

  override onChangeBusinessAndCompanyId(): void {
    this.empWorkExperience = null;
    if (this.employeeInstanceId > 0 && this.checkNoBusinessOrCompany()) {
      this.getAll();
    } else this.isLoaded = true;
  }

  getAll() {
    this.empWorkExperience = null;
    this.employeeWorkExperienceService
      .getAll(this.employeeInstanceId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IEmpWorkExperienceModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          var filter: IEmpWorkExperienceModel[] = res.data;
          filter.sort((a, b) => new Date(b.to_Date).getTime() - new Date(a.to_Date).getTime());
          this.empWorkExperience = filter[0];
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  openAddEditDialog(): void {
    this.dialogsService
      .addEditDialog(EmployeeWorkExperienceDialogComponent)
      .afterClosed()
      .subscribe((isHaseChange: boolean) => {
        if (isHaseChange) {
          this.isLoaded = false;
          this.getAll();
        }
      });
  }
}
