import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatDialogRef } from "@angular/material/dialog";
import { MatTableDataSource } from "@angular/material/table";
import { IMilitaryStatusModel } from "app/hr-setup/models/military";
import { employeeMilitaryStatusToMap } from "app/personnel-setup/mapper/employee-personal-info";
import {
  IEmployeeMilitaryDeleteParams,
  IEmployeeMilitaryModel,
  IEmployeeMilitaryRequest,
} from "app/personnel-setup/models/emp-personal-info/military-status";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import { EmployeeMilitaryDataService } from "app/personnel-setup/services/employees/personal-info/employee-military-data.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { ViewsManager } from "app/shared/resources/views-manager";
import { CustomValidator } from "app/shared/validators/custom.validator";
import { CustomValidators } from "ngx-custom-validators";

@Component({
  selector: "app-employee-military-data-dialog",
  templateUrl: "./emp-military-data-dialog.component.html",
  styleUrls: ["./emp-military-data-dialog.component.scss"],
})
export class EmployeeMilitaryDataDialogComponent
  extends AppHelpers
  implements OnInit
{
  employeeId: number = 0;
  updated: boolean = false;

  mandatoryColomns: string[] = [
    "Ser",
    // "id",
    "status",
    "grade",
    "statusDate",
    "endDate",
    "actions",
  ];
  optionalColomns: string[] = [];
  dataSource: MatTableDataSource<IEmployeeMilitaryModel>;

  isLoaded: boolean = false;

  form: FormGroup;
  submitted: boolean = false;

  selectedStatus: IMilitaryStatusModel;

  constructor(
    private employeeMilitaryDataService: EmployeeMilitaryDataService,
    public dialogRef: MatDialogRef<EmployeeMilitaryDataDialogComponent>
  ) {
    super();
    this.employeeId = EmpGeneralInfoService.EmployeeId;

    this.rowFunctions = this.getSubRowFunctions(
      this.getParentViewName,
      ViewsManager.emp_military_data_sec
    );
  }

  ngOnInit(): void {
    this.form = this.fb.group(
      {
        id: [0, { nonNullable: true }],
        militaryID: [null, [Validators.required]],
        gradeID: [null],
        statusDate: [null, [CustomValidators.date]],
        endDate: [null, [CustomValidators.date]],
      },
      {
        validator: CustomValidator.compareDates("statusDate", "endDate"),
      }
    );

    this.dialogRef.backdropClick().subscribe((ele) => {
      this.onCloseDialog();
    });
  }

  onCloseDialog() {
    this.dialogRef.close(this.updated);
  }

  get formCtrls() {
    return this.form.controls;
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany()) {
      if (this.employeeId > 0) this.getAll();
    }
  }

  getAll() {
    this.employeeMilitaryDataService
      .getAll(this.employeeId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IEmployeeMilitaryModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(
            employeeMilitaryStatusToMap(res.data)
          );
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IEmployeeMilitaryRequest = {
        EmployeeData: {
          PersonID: this.employeeId,
          militaryID: this.form.value.militaryID,
        },
      };
      if (this.selectedStatus.showGrade)
        model.EmployeeData.gradeID = this.form.value.gradeID;

      if (this.selectedStatus.dateMandatory > 0) {
        model.EmployeeData.statusDate = HelpersFunctions.mapDateReverse(
          this.form.value.statusDate
        );
        if (this.selectedStatus.dateMandatory == 2)
          model.EmployeeData.endDate = HelpersFunctions.mapDateReverse(
            this.form.value.endDate
          );
      }

      this.create(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IEmployeeMilitaryRequest) {
    this.showLoading();
    this.employeeMilitaryDataService
      .create(model)
      .subscribe((res: IResponse<IEmployeeMilitaryModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          this.updated = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  // update(model: IEmployeeMilitaryRequest) {
  //   this.showLoading();
  //   this.employeeMilitaryDataService
  //     .update(model)
  //     .subscribe((res: IResponse<IEmployeeMilitaryModel>) => {
  //       if (res.message.messageType == MessageTypes.Success) {
  //         this.clearForm();
  //         this.updateSuccessfullyMsg(res?.message?.messageTypeName);
  //         this.getAll();
  //         this.updated = true;
  //       } else this.errorList(res.message.messages);
  //       this.hideLoading();
  //     });
  // }

  // employeeMilitaryToEdit(employeeMilitaryInfo: IEmployeeMilitaryModel) {
  //   this.form.patchValue(employeeMilitaryInfo);
  //   // console.log(this.form.value);
  // }

  onDelete(employeeMilitaryInfo: IEmployeeMilitaryModel): void {
    let msg = employeeMilitaryInfo.status;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(employeeMilitaryInfo);
    });
  }

  delete(employeeMilitaryInfo: IEmployeeMilitaryModel) {
    let deleteParams: IEmployeeMilitaryDeleteParams = {
      PersonID: this.employeeId,
      MilitaryID: employeeMilitaryInfo.militaryID,
    };
    this.showLoading();
    this.employeeMilitaryDataService
      .Delete(deleteParams)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          this.updated = true;
          // if (employeeMilitaryInfo.id == this.formCtrls.id.value)
          //   this.clearForm();
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
  }

  onChangeStatus(values: IMilitaryStatusModel[]) {
    this.selectedStatus = values?.length > 0 ? values[0] : null;
    if (this.selectedStatus?.showGrade)
      this.formCtrls.gradeID.setValidators([Validators.required]);
    else this.formCtrls.gradeID.clearValidators();
    this.formCtrls.gradeID.updateValueAndValidity();
  }
}
